.wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: blueviolet;
}

.body {
  width: 500px;
  background: white;
  height: 300px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.progress {
  display: flex;
  align-items: center;
  background: #908d8d;
  border-radius: 5px;
  width: 300px;
  height: 10px;
}

.progress-bar {
  background: teal;
  width: 50px;
  height: 8px;
  border-radius: 5px;
}

.list-answer {
  list-style: none;
  padding-left: 0;
  margin-bottom: 20px;
}

.answer {
  width: 400px;
  margin-top: 5px;
  padding: 5px;
  border: 1px solid teal;
  border-radius: 10px;
  cursor: pointer;
}
