.wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: blueviolet;
}

.body {
  width: 500px;
  background: white;
  height: 300px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

img {
  width: 113px;
  height: 113px;
  background-color: white;
}

.btn-restart {
  background: #b228cdf0;
  width: 200px;
  border-radius: 10px;
  height: 40px;
  cursor: pointer;
}
